* {
  box-sizing: border-box;
}

:before, :after {
  box-sizing: inherit;
}

body, h1, h2, h3, h4, h5, h6, p, ul, input, button {
  margin: 0;
  padding: 0;
}

ol, ul {
  list-style: none;
}

img, picture, video, canvas, svg {
  width: 100%;
  display: block;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

a {
  color: inherit;
  text-decoration: none;
  display: block;
}

button {
  cursor: pointer;
  background: none;
  border: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

label {
  cursor: pointer;
}

input {
  background: none;
  border: none;
  outline: none;
}

textarea {
  outline: none;
}

